exports.onRouteUpdate = () => {
  if (window.Flickity && document.querySelector(".main-gallery")) {
    const Flickity = window.Flickity

    new Flickity(".main-gallery", {
      cellAlign: "left",
      contain: true,
      pageDots: false,
      autoPlay: 3000,
      lazyLoad: true,
    })
  }
}
